.header {
  display: flex;
  justify-content: center;
  width: 100%;
  background: #1c1c21;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 88px;
  max-width: 1280px;
  box-sizing: border-box;
  margin: 0;
  padding: 16px;
}

.leftBlock {
  display: flex;
  gap: 8px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.logoBlock {
  position: absolute;
  top: 16px;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  width: fit-content;
}

.button {
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--text-inactive-color);
  box-sizing: border-box;
  margin: 0;
  padding: 16px 20px;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  background: linear-gradient(
    to left,
    var(--text-inactive-color),
    rgb(214, 248, 246)
  );
  background-size: 100% 1000px;
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: background 15s linear infinite;
}

.button:hover {
  background: linear-gradient(
    to left,
    rgb(211, 108, 185),
    white,
    rgb(211, 108, 185),
    white,
    rgb(211, 108, 185)
  );
  background-size: 100% 1000px;
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: bounce 0.6s;
}

.button_active {
  display: flex;
  align-items: center;
  gap: 8px;
  box-sizing: border-box;
  margin: 0;
  padding: 16px 20px;
  text-decoration: none;
  transition: all 0.5s ease-in-out;
  background: linear-gradient(
    to left,
    var(--text-primary-color),
    var(--text-primary-color),
    var(--text-primary-color),
    var(--text-primary-color),
    var(--text-primary-color),
    rgb(148, 250, 241),
    var(--text-primary-color)
  );
  background-size: 100% 1000px;
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: background 40s linear infinite;
  cursor: auto;
}

@keyframes bounce {
  0%,
  20%,
  60%,
  100% {
    transform: translateY(0);
    transform: translateY(0);
  }

  40% {
    transform: translateY(-5px);
    transform: translateY(-5px);
  }

  80% {
    transform: translateY(-2px);
    transform: translateY(-px);
  }
}

@keyframes background {
  0% {
    background-position: 0;
  }
  100% {
    background-position: 2000px;
  }
}

.button_active svg {
  fill: white;
}

.logo {
  width: 289px;
}
